@import url(https://fonts.googleapis.com/css?family=Lato:200, 400);
@import url(https://fonts.googleapis.com/css?family=Lustria);
.Hyph {
    margin-right: 5px;
    margin-left: 5px;
  }
  
  .Input,
  .Textarea {
    border: 1px solid #ddd;
    display: block;
    font-size: 1.1em;
    margin-bottom: 10px;
    padding: 10px;
    text-align: left;
    width: 100%;
    border-radius: 4px;
  }
  
  .Input::-webkit-input-placeholder, .Textarea::-webkit-input-placeholder {
    color: #ccc;
  }
  
  .Input:-ms-input-placeholder, .Textarea:-ms-input-placeholder {
    color: #ccc;
  }
  
  .Input::-ms-input-placeholder, .Textarea::-ms-input-placeholder {
    color: #ccc;
  }
  
  .Input::placeholder,
  .Textarea::placeholder {
    color: #ccc;
  }
  
  .Textarea {
    resize: vertical;
  }
  
  .Button {
    order: 1px solid #aaa;
    background-color: #eee;
    font-size: 1.1em;
    border-radius: 4px;
    padding: 8px 12px;
    font-weight: bold;
  }
  
  .Required {
    color: #CA1551;
    font-family: Arial;
    font-weight: bold;
  }
  
  .Section {
    margin-left: 20px;
    margin-right: 20px;
  }
  .Section--list {
    margin-left: 10px;
    margin-right: 10px;
  }
  .Header {
    height: 40px;
    display: flex;
    align-items: normal;
    justify-content: space-between;
  }
  
  .Header h1 {
    margin: 0;
  }
  
  .Header h1 a {
    text-decoration: none;
  }
  
  .Header a {
    color: #444;
  }
  
  .Header__not-logged-in {
    display: flex;
    flex-direction: column;
  }
  
  .Header__not-logged-in .Hyph {
    display: none;
  }
  
  @media screen and (min-width: 450px) {
    .Header {
      align-items: baseline;
    }
    .Header h1 {
      font-size: 22pt;
    }
  
    .Header__not-logged-in {
      flex-direction: row;
    }
  
    .Header__not-logged-in .Hyph {
      display: inline;
      display: initial;
    }
  }
.ListWishList {
    background: none;
    font-size: smaller;
    display: block;
    padding: 10px 10px 0;
    border-bottom: black solid 1px;
    text-align: center;
    width: 100%;
    text-decoration: none;
  }

  .ListWishList__Container{
    display: flex;
    flex-direction: column;
  }

  .Button__DeleteList{
    background-color: rgba(31, 31, 41, 0.824);
    border: none;
    color: rgb(197, 194, 228);
    font-size: 10px;
  }
  
  .ListWishList:hover {
    cursor: pointer;
    background-color: #f7f8f9;
  }
  
  .ListWishList,
  .ListWishList:visited {
    color: #555;
  }
  
  .ListWishList__header {
    align-items: baseline;
    display: flex;
    justify-content: center;
  }
  .ListWishList__heading {
    margin: 0;
  }

  .ListWishList__description {
      font-style: italic;
      color: rgb(27, 32, 52)
  }
  
.Button__CreateList{
    background-color: rgba(228, 228, 250, 0.831);
    border: none;
    color: rgb(37, 35, 58);
    font-size: 13px;
    margin-top: 5px;
  }

.List_Title, .List_Description{
    margin-top: 10px; 
}
.Button__AddList{
    background-color: rgba(228, 228, 250, 0.831);
    border: none;
    color: rgb(37, 35, 58);
    font-size: 13px;
    margin-top: 10px;
  }

.WishListPage__Landing{
  background-color: rgba(238, 230, 255, 0.577);
  border-bottom: 1px solid black;
  padding: 5px;
}

.WishListPage__ButtonAndFormContainer{
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.WishListPage__Landing-demo-credentials{
  text-align: center;
  list-style-type: none;
  font-weight: 320;
}

.WishListPage__Landing-header{
  text-align: center;
}

.AddPlaceHolder{
  text-align: center;
  font-weight: 400;
  background-color: rgba(31, 30, 48, 0.886);
  color: rgb(189, 170, 228);
  text-shadow: rgb(238, 230, 255);
}
.Button__MakeNewWish{
    background-color: rgba(228, 228, 250, 0.831);
    border: none;
    color: rgb(37, 35, 58);
    font-size: 13px;
}

.Button__ChangeWish{
    background-color: rgba(228, 228, 250, 0.831);
    border: none;
    color: rgb(37, 35, 58);
    font-size: 13px;
    margin-bottom: 5px;
}
.Button__EditWish{
    background-color: rgba(228, 228, 250, 0.831);
    border: none;
    color: rgb(37, 35, 58);
    font-size: 10px;
    margin-bottom: 2px;
  }

.Button__DeleteWish{
    background-color: rgba(31, 31, 41, 0.824);
    border: none;
    color: rgb(197, 194, 228);
    font-size: 10px;
}

  .WishList__wish{
      display: flex;
      flex-direction: column;
      border-bottom: 1px solid black;
  }

  .WishList__wish_title{
      font-weight: 350;

  }
.Button__AddWish{
    background-color: rgba(228, 228, 250, 0.831);
    border: none;
    color: rgb(37, 35, 58);
    font-size: 13px;
    text-shadow: rgba(255, 255, 255, 0.995);
  }
.App {
    height: 100vh;
    padding-top: 20px;
    padding-bottom: 20px;
    max-width: 500px;
    margin: 0 auto;
  }
  
  .App__header {
    padding-left: 20px;
    padding-right: 20px;
  }
  
  .App__main {
    height: calc(100vh - 70px);
    overflow: auto;
    margin-top: 10px;
    padding-bottom: 40px;
  }
* {
  box-sizing: border-box;
}

